@import url("https://fonts.googleapis.com/css2?family=Bebas+Neue&family=Bree+Serif&family=Cormorant+Garamond:wght@300&family=Handjet:wght@500&family=Mouse+Memoirs&family=Mukta:wght@300&family=Noto+Sans:wght@500&family=Nova+Square&family=Poppins&family=Roboto+Condensed:wght@300&family=Roboto+Mono&family=Roboto+Serif:opsz,wght@8..144,100&family=Rubik+Iso&family=Rubik:ital,wght@0,400;0,700;1,500&family=Ubuntu:wght@300&display=swap");
@import url('https://fonts.googleapis.com/css2?family=Ubuntu&display=swap');
@tailwind base;
@tailwind components;
@tailwind utilities;
* {
  margin: 0;
  padding: 0;
}

.header {
  box-shadow: rgba(0, 0, 0, 0.15) 1.95px 1.95px 2.6px;
  padding: 12px 0px;
  color: gray;
  font-family: "Ubuntu", sans-serif;
}

.flex {
  display: flex;
  justify-content: space-between;
  margin: 10px 32px;
  align-items: center;
}

.inputs {
  margin: 50px 165px;
}

ul li {
  margin-right: 10px;
}

.body input {
  border: 1px solid;
  margin-left: 10px;
}

.body button {
  background-color: black;
  color: white;
  padding: 5px 10px;
  font-size: 14px;
  border-radius: 4px;
  margin-left: 10px;
}

.body input {
  /* border-top: none;
    border-right: none;
    border-left: none; */
  border: 1px solid gray;
  width: 20vw;
  padding: 3px 4px;
}

.inp {
  width: 300px;
}

.body input:focus {
  outline: none;
}

.flex button {
  background-color: black;
  font-size: 12px;
  color: white;
  padding: 5px 10px;
  border-radius: 3px;
}

.objects {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  margin: 12px 65px;
}

.cards {
  margin-right: 20px;
  margin-top: 26px;
  font-family: 'Ubuntu', sans-serif;
}

.cardComponent img {
  width: 200px;
  border-radius: 7px;
}

.scards {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.scard {
  margin: 10px 10px;
  width: 200px;
  height: 300px;
  background-color: #f0f0f0;
}

.label {
  background-color: chocolate;
  color: white;
  padding: 5px;
  font-size: 10px;
  position: absolute;
}

.contentBar {
  display: flex;
  margin: 50px 65px;
  align-items: center;
  margin-top: -10px;
}

.inp {
  background-color: #f0f0f0;
  width: 200px;
  height: 25px;
  margin-right: 10px;
}

.search {
  background-color: #f0f0f0;
  width: 40px;
  height: 28px;
}

.resItemsCont{
  margin: 0px 300px;
}

.titles {
  margin-bottom: 22px;
  font-weight: bold;
  font-size: 18px;
  color: rgb(67, 67, 67);
  cursor: pointer;
}

.upperCont {
  display: flex;
  flex-direction: column;
  /* align-items: center; */
  /* margin: 22px 0px; */
  margin-top: 40px;
  margin-bottom: 15px;
  color: gray;
  font-size: 12px;
}

.headingUpperCont{
  color: black;
  font-size: 18px;
  font-weight: bold;
  font-family: "Ubuntu", sans-serif;
}

.itemsHeading {
  display: flex;
  justify-content: space-between;
  cursor: pointer;
}

.cardComponent img {
  height: 144px;
  object-fit: cover;
}

.topImg {
  overflow-x: hidden;
  display: flex;
}

.topCont {
  margin: 42px 161px;
}

.topCont h1 {
  font-weight: bold;
  font-size: 22px;
}

.topImg img {
  width: 11vw;
  max-width: none;
  margin-right: 15px;
}

.cardComponent {
  font-family: Arial, Helvetica, sans-serif;
}

.topBarItems {
  margin: 50px;
  color: rgb(57, 57, 57);
}

.topBarItems h1 {
  font-size: 38px;
  font-weight: bolder;
  font-family: ProximaNova, arial, Helvetica Neue, sans-serif;
}

.listsMainCont h1 {
  color: black;
  font-size: 22px;
  font-family: Arial, Helvetica, sans-serif;
  margin-top: 30px;
}

.headingList {
  display: flex;
  /* justify-content: center; */
}

.lists {
  color: rgb(112, 112, 112);
  margin-right: 25px;
  margin-bottom: 30px;
  /* font-family: "Basis Grotesque Pro", Arial, sans-serif !important; */
  /* font-weight: 100; */
  line-height: normal;
  font-size: 14px;
}

.lists img {
  width: 250px;
  height: 149px;
  object-fit: cover;
  border-radius: 10px;
  margin-bottom: -22px;
}

.cuisinesList {
  width: 200px;
}

.listGroup {
  display: flex;
  flex-wrap: wrap;
  /* justify-content: center; */
  margin: 22px 0px;
}

.lists h1 {
  font-family: "Ubuntu", sans-serif;
  font-size: 17px;
  font-weight: bold;
  color: rgb(69, 69, 69);
}

.spanList{
  display: flex;
  align-items: center;
}

.icon{
  margin-right: 3px;
}

.topBar{
  display: flex;
  margin: 100px 150px;
}

.topItems{
  background-color: #f0f0f0;
  border-radius: 100px;
  height: 100px;
  width: 100px;
  margin: 10px 12px ;
}

.mainShimmerCont{
  margin: 120px;
}

.contentTime{
  display: flex;
  color: rgba(29, 29, 29, 0.763);
  font-weight: bold;
  font-size: 14px;
  margin-bottom: 15px;
  border-top: 1px dotted rgb(143, 142, 142);
  padding-top: 15px;
}

.timeDiv{
  margin-right: 30px;
  display: flex;
}

.offerDiv{
  display: flex;
  font-size: 12px;
  font-family:'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  color: gray;
  font-weight: bold;
}

.meta{
  padding: 10px;
  border: 1px solid rgb(198, 197, 197);
  margin-right: 7px;
  border-radius: 10px;
  margin-bottom: 16px;
}

.metaCont{
  display: flex;
}

.metaCont img{
  width: 20px;
  height: 20px;
  margin-right: 7px;
}

.timeDiv img{
  width: 20px;
  height: 20px;
  margin-right: 7px;
}

.itemsCont{
  padding-top: 12px;
}

.downArrow{
  width: 20px;
  height: 20px;
}

.homePath{
  margin-top: 25px;
  font-size: 9px;
  color: gray;
}

.contentItems{
  font-family: sans-serif;
}

.nameItems{
  color: rgb(90, 90, 90);
  font-weight: bold;
}

.priceItems{
  color: rgb(90, 90, 90);
  font-size: 13px;
}

.descItems{
  color: rgb(151, 151, 151);
  font-size: 13px;
  font-weight: lighter;
  padding-bottom: 20px;
}

.menuItems{
  display: flex;
  justify-content: space-between;
  margin-bottom: 30px;
}

.itemImgBtn{
  margin-left: 70px;
}

.menuItems img{
  width: 130px;
  border-radius: 10px;
  height: 100px;
  object-fit: cover;
  position: absolute;
}

.menuBtn{
  position: relative;
  background-color: white;
  font-size: 11px;
  color: rgb(4, 168, 4);
  padding: 5px 24px;
  font-weight: bold;
  border-radius: 4px;
  left: 35%;
  top: 80%;
  z-index: 10;
  border: 1px solid;
}

.cartItemsCont{
  display: flex;
  justify-content: center;
  align-items: center;
  width: 50%;
  margin: auto;
  margin-top: 40px;
  box-shadow: rgba(99, 99, 99, 0.2) 0px 2px 8px 0px;
  padding: 20px;
  padding-top: 33px;
}

.cartNameItem{
  padding-top: 14px;
}

.totalAmountDiv{
  background-color: white;
  border: 1px solid green;
  color: rgb(3, 172, 3);
  border-radius: 5px;
  /* width: 50%; */
  padding: 5px;
  margin-top: 10px;
}

.cardName{
  font-size: 16px;
  color: rgb(60, 56, 56);
  font-weight: bold;
  margin-top: 10px;
}

.cardPrice{
  font-size: 14px;
  color: rgb(60, 56, 56);
  font-weight: bold;
}

.avgRating{
  display: flex;
  color: rgb(60, 56, 56);
  font-weight: bold;
  font-size: 13px;
  align-items: center;
  font-family:'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}

.onlineList{
  display: flex;
  align-items: center;
}

.onlineList img{
  width: 30px;
  margin: 0px 5px;
}

.loginBtn{
  background-color: black;
  color: white;
  padding: 5px 10px;
  font-size: 14px;
  border-radius: 4px;
  margin-left: 10px;
}

.loginBar{
  margin: auto;
  width: 30%;
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  margin-top: 50px;
  /* padding: 50px; */
  border-radius: 10px;
  box-shadow: rgba(0, 0, 0, 0.15) 1.95px 1.95px 2.6px;
}

.inputFields input{
  border: 1px solid;
  margin-bottom: 20px;
  padding: 5px;
  width: 23vw;
  border: 1px solid gray;
}

.inputFields input:focus{
  outline: none;
}

.logBtn button{
  background-color: #f24709;
  border: 1px solid #ff4500;
  border-radius: 6px;
  padding: 5px 30px;
  color: white;
}

.toggleSign{
  cursor: pointer;
}

.text-red{
  color: red;
}

.userLog{
  display: flex;
  align-items: center;
}

.userLog p{
  margin-right: 12px;
}

.userData{
  display: flex;
  align-items: center;
  margin-left: 30px;
}

.userData img{
  width: 25px;
  margin-right: 10px;
}

.userData p{
  font-weight: bold;
}

.text-black{
  color: black;
  font-weight: bold;
}

.userDiv{
  display: flex;
  align-items: center;
}

.inputFields{
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.toggleSign{
  margin: 12px 0px;
  font-size: 14px;
  color: gray;
}

.logHeading{
  background-color: #f24709;
  margin-bottom: 52px;
  font-size: 32px;
  text-align: center;
  color: white;
  padding: 10px;
  width: -webkit-fill-available;
}

.logo{
  width: 50px;
}

.menuList{
  display: flex;
}

.hamBurger{
  display: none;
}

@media only screen and (max-width: 390px) {

  .logo{
    width: 28px;
  }

  .onlineList{
    font-size: 12px;
  }

  .logHeading{
    font-size: 22px;
  }

  .logBtn{
    font-size: 18px;
  }

  .userDiv{
    font-size: 13px;
    position: absolute;
    top: 12%;
    right: 3%;
    /* flex-direction: column; */
    align-items: center;
    padding: 11px 11px;
    background-color: #fb7443;
    /* border-radius: 6px; */
  }

  .userDiv li{
    color: white;
    /* margin-bottom: 7px; */
    /* padding-bottom: 2px; */
    font-weight: normal;
  }

  .userData{
    margin-left: 0px;
    /* margin-bottom: 7px; */
    color: white;
  }

  .userData img{
    margin-right: 3px;
    width: 20px;
  }

  .flex button{
    background-color: transparent;
    padding: 3px 4px;
    font-size: 7px;
    border: 1px solid;
  }

  .userLog{
    /* flex-direction: column; */
  }

  .menuList{
    align-items: center;
  }

  .hamBurger{
    display: block;
  }

  .loginBar{
    width: 55%;
  }

  .inputFields input{
    width: auto;
  }

  .topCont{
    margin: 42px 29px;
  }

  .topCont h1{
    font-size: 14px;
  }

  .inputs{
    margin: 28px 33px;
  }

  .body input{
    width: 38vw;
    font-size: 9px;
  }
}